<script>

import MainContent from "@/components/Content.vue"
export default {
	name: 'App',
	components: {
		MainContent
	},

	data: () => {
		return {
			check: false
		}
	},

	computed: {
		index () {
			if (this.check) {
				return this.$carousel.index;
			} else return -1;
		}
	},

	methods: {
		handle($carousel) {
			this.check = true;
			this.$carousel = $carousel
		},
		to(n) {
			if(this.check) {
				this.$carousel.slideTo(n)
			}
		},
/*		check() {
			if (this.$carousel) return true;
			if (this.$refs.maincontent)
			if (this.$children[1].$carousel) {
				
				return true;
			} else {
				return false;
			}
		},*/
		active(n) {
			if(this.check) {
				return this.index == n
			} else {
				return false
			}
		}
	}
}
</script>

