<template>
	<carousel 
		class="wrapper"
		:data="data"
		:controls="true"
		:indicators="true" 
		:autoplay="false"
		:interval="10000000"
		direction="up"

	></carousel>
</template>

<script>
import firstSlide from "./FirstSlide.vue"
import secondSlide from "./SecondSlide.vue"
import thirdSlide from "./ThirdSlide.vue"
import fourthSlide from "./FourthSlide.vue"
//import fivethSlide from "./FivethSlide.vue"
import sixthSlide from "./SixthSlide.vue"
import seventhSlide from "./SeventhSlide.vue"
import taskSlide from "./TaskSlide.vue"

	export default {
		data() {
			return {
				data: [
					{ content(createElement) { return createElement(firstSlide);},},
					{ content(createElement) { return createElement(secondSlide);},},
					{ content(createElement) { return createElement(thirdSlide);},},
					{ content(createElement) { return createElement(fourthSlide);},},
					{ content(createElement) { return createElement(taskSlide);},},
				//	{ content(createElement) { return createElement(fivethSlide);},},
					{ content(createElement) { return createElement(sixthSlide);},},
					{ content(createElement) { return createElement(seventhSlide);},},
				],
			};
		},
		mounted () {
			this.$carousel = this.$children[0];
			this.$emit('check', this.$carousel)
		}
	};
</script>

